













import Vue from "vue";

export default Vue.extend({
  name: "NotificationModal",

  data() {
    return {
      show: false,
      content: {
        title: "",
        body: "",
      },
    };
  },

  methods: {
    open(options: { title: string; body: string }) {
      if (options) this.content = options;
      this.show = true;
    },
    close() {
      this.show = false;
    },
  },
});
