









import Vue from "vue";

export default Vue.extend({
  name: "Loading",

  data() {
    return {
      isLoading: false,
      loadingCount: 0,
    };
  },

  methods: {
    progressOn() {
      if (this.loadingCount === 0) {
        this.isLoading = true;
      }
      this.loadingCount++;
    },
    progressOff() {
      this.loadingCount--;
      if (this.loadingCount === 0) {
        this.isLoading = false;
      }
    },
  },
});
